.table {
    width: 1200px;
    margin-bottom: 1rem;
    color: #212529;
    background-color: white;
    border-radius: 8px;
}

.table thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
}

.table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
    text-align: center;
}

.link {
    cursor: pointer;
}

.link:hover {
    opacity: 0.6;
    color: #5264AE;
}